<template>
  <aside class="filter">
    <form class="filter__form form">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom"/>
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="currentPriceTo"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentCategoryId">
            <option value="0" selected disabled>Все категории</option>
            <option
              v-for="category in categories"
              :key="category.id"
              :value="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>

      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors--black">
        <li
          v-for="productColor in colors"
          :key="productColor.id"
          class="colors__item"
        >
          <label class="colors__label">
            <input
              :value="productColor.id"
              v-model="currentColorIds"
              class="colors__radio sr-only"
              type="checkbox"
              name="colors"
            />
            <span class="colors__value" :style="{ backgroundColor: productColor.code }"></span>
          </label>
        </li>
      </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li
            v-for="material in materials"
            :key="material.id"
            class="check-list__item"
          >
            <label class="check-list__label">
              <input
                type="checkbox"
                class="check-list__check sr-only"
                name="material"
                :value="material.id"
                v-model="currentMaterialIds"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li
            v-for="season in seasons"
            :key="season.id"
            class="check-list__item"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                :value="season.id"
                v-model="currentSeasonIds"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit" @click.prevent="submit">
        Применить
      </button>
      <button
        class="filter__reset button button--second"
        type="button"
        @click.prevent="reset"
        v-show="showing"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios'
import { BASE_API_URL } from '@/config'

export default {
  data () {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorIds: [],
      currentMaterialIds: [],
      currentSeasonIds: [],

      categoriesData: null,
      colorsData: null,
      seasonsData: null,
      materialsData: null
    }
  },
  props: {
    priceFrom: {
      type: Number,
      default: 0
    },
    priceTo: {
      type: Number,
      default: 0
    },
    categoryId: {
      type: Number,
      default: 0
    },
    colorsArray: {
      type: Array
    },
    materialsArray: {
      type: Array
    },
    seasonsArray: {
      type: Array
    }
  },
  computed: {
    categories () {
      return this.categoriesData ? this.categoriesData : []
    },
    colors () {
      return this.colorsData ? this.colorsData : []
    },
    seasons () {
      return this.seasonsData ? this.seasonsData : []
    },
    materials () {
      return this.materialsData ? this.materialsData : []
    },
    showing () {
      if (this.currentPriceFrom > 0 ||
          this.currentPriceTo > 0 ||
          this.currentCategoryId !== 0 ||
          this.currentColorIds.length > 0 ||
          this.currentMaterialIds.length > 0 ||
          this.currentSeasonIds.length > 0) {
        return true
      }

      return false
    }
  },
  watch: {
    priceFrom (value) {
      this.currentPriceFrom = value
    },
    priceTo (value) {
      this.currentPriceTo = value
    },
    categoryId (value) {
      this.currentCategoryId = value
    }
  },
  emits: [
    'update:price-from',
    'update:price-to',
    'update:category-id',
    'update:colors-array',
    'update:materials-array',
    'update:seasons-array'
  ],
  methods: {
    submit () {
      this.$emit('update:price-from', this.currentPriceFrom)
      this.$emit('update:price-to', this.currentPriceTo)
      this.$emit('update:category-id', this.currentCategoryId)
      this.$emit('update:colors-array', this.currentColorIds)
      this.$emit('update:materials-array', this.currentMaterialIds)
      this.$emit('update:seasons-array', this.currentSeasonIds)
    },
    reset () {
      this.currentCategoryId = 0
      this.currentPriceFrom = 0
      this.currentCategoryId = 0
      this.currentColorIds = []
      this.currentMaterialIds = []
      this.currentSeasonIds = []

      this.$emit('update:price-from', 0)
      this.$emit('update:price-to', 0)
      this.$emit('update:category-id', 0)
      this.$emit('update:colors-array', [])
      this.$emit('update:materials-array', [])
      this.$emit('update:seasons-array', [])
    },
    getCategories () {
      axios
        .get(`${BASE_API_URL}/api/productCategories`)
        .then((response) => { this.categoriesData = response.data.items })
    },
    getSeasons () {
      axios
        .get(`${BASE_API_URL}/api/seasons`)
        .then((response) => { this.seasonsData = response.data.items })
    },
    getMaterials () {
      axios
        .get(`${BASE_API_URL}/api/materials`)
        .then((response) => { this.materialsData = response.data.items })
    },
    getColors () {
      axios
        .get(`${BASE_API_URL}/api/colors`)
        .then((response) => { this.colorsData = response.data.items })
    },
    pushColors (value) {
      this.currentColorIds.push(value)
    }
  },
  created () {
    this.getCategories()
    this.getSeasons()
    this.getMaterials()
    this.getColors()
  }
}
</script>
