<template>
  <div v-if="productsLoadingStatus">
    <img
      class="product__preloader"
      src="/moire/img/preloader.gif"
      alt="Идет загрузка.."
    />
  </div>

  <div
    class="product__loading-error"
    v-if="productsLoadingFailed"
  >
    Произошла ошибка при загрузке

  <button
    class="product__reload-btn"
    @click.prevent="loadProducts"
  >
    Попробовать еще раз
  </button>
  </div>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'main' }"> {{ categoryTitle }} </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <p class="breadcrumbs__link"> {{ title }} </p>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :alt="title"
            :src="currentImage ? currentImage : 'https://t-bike.ru/images/products/no-image.jpg'"
          />
        </div>
        <ul class="pics__list">
          <li
            class="pics__item"
            v-for="image in otherImages"
            :key="image.id"
          >
            <a class="pics__link pics__link--current">
              <img
                width="98"
                height="98"
                :alt="title"
                :src="image.gallery !== null ? image.gallery[0].file.url : 'https://t-bike.ru/images/products/no-image.jpg'"
              />
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ id }}</span>
        <h2 class="item__title">{{ title }}</h2>
        <div class="item__form">
          <form class="form" action="#" method="POST">
            <div class="item__row item__row--center">
              <ProductCounter
                v-model:amount="amount"
                :min="1"
                :max="100"
              />

              <b class="item__price"> {{ numberFormat(calcSum) }} ₽ </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li
                    class="colors__item"
                    v-for="productColor in colorsArray"
                    :key="productColor.id"
                  >
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        name="color-item"
                        :value="productColor.id"
                        v-model="colorId"
                      />
                      <span
                        class="colors__value"
                        :style="{ backgroundColor: productColor.code }"
                      >
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label
                  class="form__label form__label--small form__label--select"
                >
                  <select class="form__select" type="text" name="category" v-model="sizeId">
                    <option
                      v-for="size of sizes"
                      :key="size.id"
                      :value="size.id"
                    >
                      {{ size.title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit" @click.prevent="addToCart">
              <p class="item__button-text" v-if="productAddSending">
                Добавляем...
              </p>
              <p class="item__button-text" v-else-if="productAdded">
                Добавлено
              </p>
              <p class="item__button-text" v-else>
                В корзину
              </p>
            </button>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"> Информация о товаре </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Доставка и возврат </a>
          </li>
        </ul>

        <div class="item__content">
          <h3>Состав:</h3>

          <p>
            60% хлопок<br />
            30% полиэстер<br />
          </p>

          <h3>Уход:</h3>

          <p>
            Машинная стирка при макс. 30ºC короткий отжим<br />
            Гладить при макс. 110ºC<br />
            Не использовать машинную сушку<br />
            Отбеливать запрещено<br />
            Не подвергать химчистке<br />
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { BASE_API_URL } from '@/config'
import axios from 'axios'
import numberFormat from '@/helpers/numberFormat'
import ProductCounter from '@/components/products/ProductCounter'
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      amount: 1,
      colorId: 0,
      sizeId: 0,
      productData: null,

      productsLoadingStatus: false,
      productsLoadingFailed: false,

      productAdded: false,
      productAddSending: false
    }
  },
  components: {
    ProductCounter
  },
  computed: {
    product: {
      get () {
        if (this.productData) {
          return this.productData
        }

        return undefined
      },
      cache: false,
      lazy: true
    },
    id: {
      get () {
        if (this.product) {
          return this.product.id
        }

        return undefined
      },
      cache: false,
      lazy: true
    },
    category: {
      get () {
        if (this.product) {
          return this.product.category
        }

        return undefined
      },
      cache: false,
      lazy: true
    },
    categoryTitle: {
      get () {
        if (this.category) {
          return this.category.title
        }

        return ''
      },
      cache: false,
      lazy: true
    },
    title: {
      get () {
        if (this.product) {
          return this.product.title
        }

        return ''
      },
      cache: false,
      lazy: true
    },
    price: {
      get () {
        if (this.product) {
          return this.product.price
        }

        return 0
      },
      cache: false,
      lazy: true
    },
    colors: {
      get () {
        if (this.product) {
          return this.product.colors
        }

        return []
      },
      cache: false,
      lazy: true
    },
    sizes: {
      get () {
        if (this.product) {
          return this.product.sizes
        }

        return []
      },
      cache: false,
      lazy: true
    },
    calcSum () {
      return this.amount * this.price
    },
    currentImage: {
      get () {
        if (this.product) {
          const object = this.colors.find((item) => (
            item.color.id === this.colorId
          ))

          if (object.gallery !== null) {
            return object.gallery.find((item) => (typeof item !== 'undefined')).file.url
          }
        }

        return ''
      },
      cache: false,
      lazy: true
    },
    colorsArray: {
      get () {
        if (this.product) {
          return this.parseColors(this.product.colors)
        }

        return []
      },
      cache: false,
      lazy: true
    },
    otherImages () {
      return this.colors
    }
  },
  methods: {
    ...mapActions({
      addProductToCart: 'products/addProductToCart'
    }),
    addToCart () {
      this.productAdded = false
      this.productAddSending = true

      this.addProductToCart({
        productId: this.product.id,
        colorId: this.colorId,
        sizeId: this.sizeId,
        quantity: this.amount
      })
        .then(() => {
          this.productAdded = true
          this.productAddSending = false
        })
    },
    numberFormat,
    loadProduct () {
      this.productsLoadingStatus = true
      this.productsLoadingFailed = false

      axios
        .get(`${BASE_API_URL}/api/products/` + this.$route.params.id)
        .then((response) => {
          this.productData = response.data
          this.colorId = this.productData.colors[0].color.id
          this.sizeId = this.productData.sizes[0].id
        })
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => (this.productsLoadingStatus = false))
    },
    parseColors (colors) {
      const colorsArray = []

      for (const colorObj of colors) {
        colorsArray.push(colorObj.color)
      }

      return colorsArray
    }
  },
  watch: {
    '$route.params.id': {
      handler () {
        this.loadProduct()
      },
      immediate: true
    }
  },
  created () {
    this.loadProduct()
  }
}
</script>

<style lang="scss" scoped>
.product {
  position: relative;
  height: 100px;

  &__preloader {
    padding: 30px;
    left: 50%;
    top: 10%;
    transform: translate(50%, 50%);
  }

  &__loading-error {
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 15%;
    left: 43%;
  }

  &__reload-btn {
    margin-top: 20px;
    background-color: transparent;
    border: 1px solid #222;
    padding: 15px;
    color: #222;
    font-size: 14px;
    cursor: pointer;
  }
}

.item {
  &__button-text {
    margin: 0;
    padding: 0;
  }
}
</style>
