<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <span class="breadcrumbs__link"> Оформление заказа </span>
        </li>
      </ul>

      <h1 class="content__title">{{ orderTitle }} <span>№ {{ orderId }}</span></h1>
    </div>

    <section class="cart">
      <form class="cart__form form">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <OrderInfoCustomerItem
              title="Получатель"
              :descr="customerName"
            />
            <OrderInfoCustomerItem
              title="Адрес доставки"
              :descr="customerAddress"
            />
            <OrderInfoCustomerItem
              title="Телефон"
              :descr="customerPhone"
            />
            <OrderInfoCustomerItem
              title="Email"
              :descr="customerEmail"
            />
            <OrderInfoCustomerItem
              title="Способ оплаты"
              :descr="paymentType"
            />
            <OrderInfoCustomerItem
              v-show="customerComment"
              title="Комментарий"
              :descr="customerComment"
            />
          </ul>
        </div>

        <div class="cart__block">
          <OrderProductsList
            v-if="orderItems"
            :products="orderItems"
          />

          <div
            v-if="deliveryInfo"
            class="cart__total"
          >
            <p>{{ deliveryInfo.title }}: <b>{{ deliveryInfo.price === 0 ? `${numberFormat(deliveryInfo.price)} ₽` : 'Бесплатно' }}</b></p>
            <p>Итого: <b>{{ totalAmount }}</b> товара на сумму <b>{{ totalPrice }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import OrderInfoCustomerItem from '@/components/order/OrderInfoCustomerItem.vue'
import numberFormat from '@/helpers/numberFormat'
import OrderProductsList from '@/components/order/OrderProductsList.vue'

export default {
  components: {
    OrderInfoCustomerItem,
    OrderProductsList
  },
  computed: {
    ...mapState('products', {
      orderInfo: state => state.orderInfo
    }),
    orderItems () {
      return this.orderInfo?.basket.items
    },
    totalAmount () {
      return this.orderItems.reduce((total, item) => total + item.quantity, 0) || 0
    },
    orderId () {
      return this.orderInfo?.id
    },
    totalPrice () {
      return this.numberFormat(this.orderInfo?.totalPrice)
    },
    customerName () {
      return this.orderInfo?.name
    },
    customerAddress () {
      return this.orderInfo?.address
    },
    customerPhone () {
      return this.orderInfo?.phone
    },
    customerEmail () {
      return this.orderInfo?.email
    },
    paymentType () {
      return this.orderInfo?.paymentType
    },
    customerComment () {
      return this.orderInfo?.comment ? this.orderInfo.comment : ''
    },
    deliveryInfo () {
      return this.orderInfo?.deliveryType
    },
    orderStatus () {
      return this.orderInfo?.status
    },
    orderTitle () {
      return this.orderInfo?.status.title
    }
  },
  methods: {
    ...mapActions('products', {
      loadOrderInfo: 'loadOrderInfo'
    }),
    numberFormat
  },
  created () {
    if (this.orderInfo && this.orderInfo === this.$route.params.id) {
      return
    }

    this.loadOrderInfo(this.$route.params.id)
  }
}
</script>
