<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="image"
        :alt="item.product.title"
        width="120"
        height="120"
      />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <p class="product__info product__info--color product__info--color_first">
      Цвет:
      <span>
        <i
          :style="{ backgroundColor: color.code }"
        ></i>
        {{ $t(colorTitle) }}
      </span>
    </p>
    <p class="product__info product__info--color product__info--size">
      Размер:
      <span>
        {{ size }}
      </span>
    </p>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <ProductCounter
      v-model:amount="amount"
      :value="amount"
      :min="1"
      :max="100"
      class="product__counter form__counter"
    />

    <b class="product__price"> {{ numberFormat(price) }} ₽ </b>

    <button
      @click.prevent="deleteProduct"
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="/moire/img/svg/sprite.svg#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import ProductCounter from '@/components/products/ProductCounter.vue'
import { mapActions } from 'vuex'
import numberFormat from '@/helpers/numberFormat'

export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  components: {
    ProductCounter
  },
  computed: {
    image () {
      return this.item.image
    },
    amount: {
      get () {
        return this.item.quantity
      },
      set (value) {
        this.updateCartProductAmount({
          productId: this.item.id,
          quantity: value
        })
      },
      cache: false,
      lazy: true
    },
    price () {
      return this.item.price * this.item.quantity
    },
    color () {
      return this.item.color.color
    },
    colorTitle () {
      return this.color.title.toLowerCase().trim()
    },
    size () {
      return this.item.size.title
    }
  },
  methods: {
    ...mapActions({
      deleteCartProduct: 'products/deleteCartProduct',
      updateCartProductAmount: 'products/updateCartProductAmount'
    }),
    numberFormat,
    deleteProduct () {
      this.deleteCartProduct({
        productId: this.item.id
      })
    }
  }
}
</script>

<style scoped>
.button-del {
  cursor: pointer;
}

.product__info--color_first {
  margin-bottom: 45px;
}

.product__info--size span {
  padding-left: 5px;
}
</style>
