<template>
  <VHeader />

  <RouterView />

  <VFooter />
</template>

<script>
import VFooter from '@/components/VFooter.vue'
import VHeader from '@/components/VHeader.vue'
import { mapActions, mapMutations } from 'vuex'

export default {
  components: {
    VHeader,
    VFooter
  },
  methods: {
    ...mapActions({
      loadCart: 'products/loadCart'
    }),
    ...mapMutations({
      updateUserAccessKey: 'updateUserAccessKey'
    })
  },
  created () {
    const userAccessKey = localStorage.getItem('accessKey')

    if (userAccessKey) {
      this.updateUserAccessKey(userAccessKey)
    }

    this.loadCart()
  }
}
</script>
