<template>
  <div class="cart__block">
    <OrderProductsList
      :products="products"
    />

    <div class="cart__total">
      <p>Доставка: <b>бесплатно</b></p>
      <p>
        Итого: <b>{{ totalAmount }}</b> товара на сумму <b>{{ numberFormat(totalPrice) }} ₽</b>
      </p>
    </div>

    <button
      v-show="totalAmount !== 0"
      class="cart__button button button--primery"
      type="submit"
    >
      Оформить заказ
    </button>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import OrderProductsList from '@/components/order/OrderProductsList.vue'
import { mapGetters } from 'vuex'

export default {
  components: {
    OrderProductsList
  },
  computed: {
    ...mapGetters('products', {
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalAmount: 'getTotalAmount'
    })
  },
  methods: {
    numberFormat
  }
}
</script>
