<template>
  <RouterLink class="header__cart" aria-label="Корзина с товарами" :to="{ name: 'cart' }">
    <svg width="19" height="24">
      <use xlink:href="/moire/img/svg/sprite.svg#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label="Количество товаров">
      {{ getCartProductsAmount }}
    </span>
  </RouterLink>
</template>

<script>
export default {
  computed: {
    getCartProductsAmount () {
      return this.$store.state.products.cartProductsData.length
    }
  }
}
</script>
