<template>
  <li class="catalog__item">
    <RouterLink class="catalog__pic" :to="{ name: 'product', params: { id: product.id }}">
      <img
        :src="currentImage ? currentImage : 'https://t-bike.ru/images/products/no-image.jpg'"
        :alt="product.title"
      />
    </RouterLink>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ numberFormat(product.price) }} ₽ </span>

    <ul class="colors colors--black">
      <li
        class="colors__item"
        v-for="productColor in colorsArray"
        :key="productColor.id"
      >
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            name="color"
            :value="productColor.id"
            v-model="currentColor"
          />
          <span class="colors__value" :style="{ backgroundColor: productColor.code }"> </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'

export default {
  name: 'ProductItem',
  data () {
    return {
      currentColor: 0
    }
  },
  props: {
    product: {
      type: Object
    }
  },
  computed: {
    colors () {
      return this.product.colors
    },
    currentImage: {
      get () {
        if (this.product) {
          const object = this.colors.find((item) => (
            item.color.id === this.currentColor
          ))

          if (object.gallery !== null) {
            return object.gallery.find((item) => (typeof item !== 'undefined')).file.url
          }
        }

        return ''
      },
      cache: false,
      lazy: true
    },
    colorsArray: {
      get () {
        if (this.product) {
          return this.parseColors(this.product.colors)
        }

        return []
      },
      cache: false,
      lazy: true
    }
  },
  methods: {
    numberFormat,
    parseColors (colors) {
      const colorsArray = []

      for (const colorObj of colors) {
        colorsArray.push(colorObj.color)
      }

      return colorsArray
    },
    selectColor (event) {
      const value = event.target.value
      this.currentColor = value
    },
    firstColor () {
      this.currentColor = this.colors[0].color.id
    }
  },
  created () {
    this.firstColor()
  }
}
</script>
