<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">Каталог</h1>
        <span class="content__info"> {{ countProducts }} товара </span>
      </div>
    </div>

    <div class="content__catalog">

      <ProductFilter
        v-model:price-from="filterPriceFrom"
        v-model:price-to="filterPriceTo"
        v-model:category-id="filterCategoryId"
        v-model:colors-array="filterColors"
        v-model:materials-array="filterMaterials"
        v-model:seasons-array="filterSeasons"
      />

      <section class="catalog">
        <ProductCatalog :products="products"/>

        <div v-if="productsLoadingStatus">
          <img
            class="catalog__preloader"
            src="/moire/img/preloader.gif"
            alt="Идет загрузка.."
          />
        </div>

        <div
          class="catalog__loading-error"
          v-if="productsLoadingFailed"
        >
          Произошла ошибка при загрузке

          <button
            class="catalog__reload-btn"
            @click.prevent="loadProducts"
          >
            Попробовать еще раз
          </button>
        </div>

        <VPagination
          v-model:page="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductFilter from '@/components/products/ProductFilter.vue'
import ProductCatalog from '@/components/products/ProductCatalog.vue'
import VPagination from '@/components/VPagination.vue'
import { BASE_API_URL } from '@/config'
import axios from 'axios'

export default {
  components: {
    ProductFilter,
    ProductCatalog,
    VPagination
  },
  data () {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColors: [],
      filterMaterials: [],
      filterSeasons: [],

      page: 1,
      productsPerPage: 6,

      productsData: null,

      productsLoadingStatus: false,
      productsLoadingFailed: false
    }
  },
  computed: {
    products () {
      return this.productsData
        ? this.productsData.items.map((product) => {
          return {
            ...product,
            image: this.findImage(product.colors),
            firstlyColorId: product.colors[0].color.id
          }
        })
        : []
    },
    countProducts () {
      return this.productsData ? this.productsData.pagination.total : 0
    }
  },
  methods: {
    loadProducts () {
      this.productsLoadingStatus = true
      this.productsLoadingFailed = false

      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${BASE_API_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorIds: this.filterColors,
              materialIds: this.filterMaterials,
              seasonIds: this.filterSeasons
            }
          })
          .then((response) => { this.productsData = response.data })
          .catch(() => { this.productsLoadingFailed = true })
          .then(() => { this.productsLoadingStatus = false })
      }, 0)
    },
    findImage (colors) {
      for (const color of colors) {
        if (color.gallery !== null) {
          // eslint-disable-next-line no-unreachable-loop
          for (const gallery of color.gallery) {
            return gallery.file.url
          }
          return 'https://t-bike.ru/images/products/no-image.jpg'
        }
      }
    }
  },
  created () {
    this.loadProducts()
  },
  watch: {
    page () {
      this.loadProducts()
    },
    filterPriceFrom () {
      this.loadProducts()
    },
    filterPriceTo () {
      this.loadProducts()
    },
    filterCategoryId () {
      this.loadProducts()
    },
    filterColors () {
      this.loadProducts()
    },
    filterMaterials () {
      this.loadProducts()
    },
    filterSeasons () {
      this.loadProducts()
    }
  }
}
</script>

<style lang="scss" scoped>
.catalog {
  position: relative;

  &__preloader {
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(50%, 50%);
  }

  &__loading-error {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 15%;
    left: 43%;
  }

  &__reload-btn {
    margin-top: 20px;
    background-color: transparent;
    border: 1px solid #222;
    padding: 15px;
    color: #222;
    font-size: 14px;
    cursor: pointer;
  }
}
</style>
