<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <p class="breadcrumbs__link"> Оформление заказа </p>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">Оформление заказа</h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" @submit.prevent="makeOrder">
        <div class="cart__field">
          <div class="cart__data">
            <VFormText
              v-model="formData.name"
              :error="formError.name"
              title="ФИО"
              placeholder="Введите ваше полное имя"
            />

            <VFormText
              v-model="formData.address"
              :error="formError.address"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
            />

            <VFormText
              v-model="formData.phone"
              :error="formError.phone"
              type="tel"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />

            <VFormText
              v-model="formData.email"
              :error="formError.email"
              type="email"
              title="Email"
              placeholder="Введи ваш Email"
            />

            <VFormTextareaVue
              v-model="formData.comment"
              :error="formError.comment"
              title="Комментарии к заказу"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li
                v-for="delivery in deliveries"
                :key="delivery.id"
                class="options__item"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    :value="delivery.id"
                    v-model="currentDeliveryId"
                  />
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ numberFormat(delivery.price) }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li
                v-for="payment in payments"
                :key="payment.id"
                class="options__item"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    :value="payment.id"
                    v-model="currentPaymentId"
                  />
                  <span class="options__value"> {{ payment.title }} </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <OrderProductsPanel />
        <div class="cart__error form__error-block" v-show="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import VFormText from '@/components/VFormText.vue'
import VFormTextareaVue from '@/components/VFormTextarea.vue'
import OrderProductsPanel from '@/components/order/OrderProductsPanel.vue'
import { mapMutations } from 'vuex'
import axios from 'axios'
import { BASE_API_URL } from '@/config'

export default {
  data () {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      deliveriesInfo: null,
      paymentInfo: null,
      currentDeliveryId: 0,
      currentPaymentId: 0,

      formLoading: false,

      deliveryInfoLoading: false,
      deliveryInfoLoadingFaild: false,

      paymentInfoLoading: false,
      paymentInfoLoadingFailed: false
    }
  },
  components: {
    VFormText,
    VFormTextareaVue,
    OrderProductsPanel
  },
  computed: {
    deliveries: {
      get () {
        return this.deliveriesInfo
      },
      cache: true
    },
    payments: {
      get () {
        return this.paymentInfo
      },
      cache: true
    }
  },
  methods: {
    ...mapMutations('products', {
      resetCart: 'resetCart',
      updateOrderInfo: 'updateOrderInfo'
    }),
    numberFormat,
    makeOrder () {
      this.formError = {}
      this.formErrorMessage = ''
      this.formLoading = true

      axios
        .post(
            `${BASE_API_URL}/api/orders`,
            {
              ...this.formData,
              deliveryTypeId: this.currentDeliveryId,
              paymentTypeId: this.currentPaymentId
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            }
        )
        .then((response) => {
          this.formLoading = false
          this.resetCart()
          this.updateOrderInfo(response.data)
          this.$router.push(
            {
              name: 'order-info',
              params: {
                id: response.data.id
              }
            }
          )
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {}
          this.formErrorMessage = error.response.data.error.message
          this.formLoading = false
        })
    },
    getDeliveryInfo () {
      this.deliveryInfoLoading = true
      this.deliveryInfoLoadingFaild = false

      axios
        .get(`${BASE_API_URL}/api/deliveries`)
        .then((response) => {
          this.deliveryInfoLoading = false
          this.deliveriesInfo = response.data || []
          this.currentDeliveryId = this.deliveriesInfo[0].id
        })
        .catch(() => (this.deliveryInfoLoadingFaild = true))
    },
    getPaymentInfo () {
      this.paymentInfoLoading = true
      this.paymentInfoLoadingFailed = false

      axios
        .get(`${BASE_API_URL}/api/payments`, {
          params: {
            deliveryTypeId: this.currentDeliveryId
          }
        })
        .then((response) => {
          this.paymentInfoLoading = false
          this.paymentInfo = response.data
          this.currentPaymentId = this.paymentInfo[0].id
        })
        .catch(() => (this.paymentInfoLoadingFailed = true))
    }
  },
  watch: {
    currentDeliveryId () {
      this.getPaymentInfo()
    }
  },
  created () {
    this.getDeliveryInfo()
  }
}
</script>
