<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="Убрать один товар"
      @click.prevent="decrement"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="/moire/img/svg/sprite.svg#icon-minus"></use>
      </svg>
    </button>

    <input type="text" name="count" v-model="amount" />

    <button
      type="button"
      aria-label="Добавить один товар"
      @click.prevent="increment"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="/moire/img/svg/sprite.svg#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    },
    min: {
      type: Number,
      default: 1
    },
    max: {
      type: Number,
      default: 100
    }
  },
  emits: ['update:amount'],
  data () {
    return {
      amount: this.value
    }
  },
  methods: {
    decrement () {
      if (this.amount > this.min) {
        this.amount -= 1
        this.$emit('update:amount', this.amount)
      }
    },
    increment () {
      if (this.amount < this.max) {
        this.amount += 1
        this.$emit('update:amount', this.amount)
      }
    }
  },
  watch: {
    amount () {
      if (this.amount > this.min && this.amount < this.max) {
        this.$emit('update:amount', Number(this.amount))
      }
    }
  }
}
</script>
