import axios from 'axios'
import { BASE_API_URL } from '@/config'

export const productsModule = {
  namespaced: true,
  state: () => ({
    cartProductsData: [],
    orderInfo: null,
    productsCartLoadingStatus: false,
    productsCartLoadingFailed: false
  }),
  mutations: {
    updateOrderInfo (state, orderInfo) {
      state.orderInfo = orderInfo
    },
    resetCart (state) {
      state.cartProductsData = []
    },
    updateCartProductsAmount (state, { productId, amount }) {
      const item = state.cartProductsData.find(
        (p) => p.product.id === productId
      )

      if (item) {
        item.amount = amount
      }
    },
    updateCartProductsData (state, items) {
      state.cartProductsData = items
    }
  },
  getters: {
    cartDetailProducts (state) {
      return state.cartProductsData.map((item) => {
        return {
          ...item,
          image: item.color.gallery ? item.color.gallery[0].file.url : 'https://t-bike.ru/images/products/no-image.jpg'
        }
      })
    },
    cartTotalPrice (state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => item.price * item.quantity + acc, 0)
    },
    getTotalAmount (state, getters) {
      return getters.cartDetailProducts.reduce((total, item) => total + item.quantity, 0)
    }
  },
  actions: {
    async loadCart ({ state, commit, rootState }) {
      state.productsCartLoadingStatus = true
      state.productsCartLoadingFailed = false

      return axios
        .get(`${BASE_API_URL}/api/baskets`, {
          params: {
            userAccessKey: rootState.userAccessKey
          }
        })
        .then((response) => {
          if (!rootState.userAccessKey) {
            const { accessKey } = response.data.user
            localStorage.setItem('accessKey', accessKey)
            commit('updateUserAccessKey', accessKey, { root: true })
          }

          commit('updateCartProductsData', response.data.items)
        })
        .catch(() => { state.productsCartLoadingFailed = true })
        .then(() => { state.productsCartLoadingStatus = false })
    },
    async addProductToCart ({ commit, rootState }, { productId, colorId, sizeId, quantity }) {
      return axios
        .post(
          `${BASE_API_URL}/api/baskets/products`,
          {
            productId,
            colorId,
            sizeId,
            quantity
          },
          {
            params: {
              userAccessKey: rootState.userAccessKey
            }
          }
        )
        .then((response) => {
          commit('updateCartProductsData', response.data.items)
        })
    },
    async updateCartProductAmount ({ commit, rootState }, { productId, quantity }) {
      commit(
        'updateCartProductsAmount',
        {
          productId,
          amount: quantity
        }
      )

      if (quantity < 1) {
        return
      }

      return axios
        .put(
          `${BASE_API_URL}/api/baskets/products`,
          {
            basketItemId: productId,
            quantity
          },
          {
            params: {
              userAccessKey: rootState.userAccessKey
            }
          }
        )
        .then((response) => {
          commit('updateCartProductsData', response.data.items)
        })
    },
    async deleteCartProduct ({ commit, rootState }, { productId }) {
      return axios
        .delete(
          `${BASE_API_URL}/api/baskets/products`,
          {
            data: {
              basketItemId: productId
            },
            params: {
              userAccessKey: rootState.userAccessKey
            }
          }
        )
        .then((response) => {
          commit('updateCartProductsData', response.data.items)
        })
    },
    async loadOrderInfo ({ commit, rootState }, orderId) {
      return axios
        .get(`${BASE_API_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: rootState.userAccessKey
          }
        })
        .then((response) => {
          commit('updateOrderInfo', response.data)
        })
    }
  }
}
