<template>
  <VFormField :title="title" :error="error">
    <input
      class="form__input"
      :type="type"
      :placeholder="placeholder"
      @input="updateValue"
    />
  </VFormField>
</template>

<script>
import formFieldMixin from '@/mixins/formFieldMixin'

export default {
  props: {
    type: {
      type: String,
      default: 'text'
    }
  },
  mixins: [
    formFieldMixin
  ]
}
</script>
