import { createStore } from 'vuex'
import { productsModule } from '@/store/productsModule'

export default createStore({
  state: () => ({
    userAccessKey: null
  }),
  modules: {
    products: productsModule
  },
  mutations: {
    updateUserAccessKey (state, accessKey) {
      state.userAccessKey = accessKey
    }
  }
})
