<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">Корзина</a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">Корзина</h1>
        <span class="content__info"> {{ totalAmount }} товара </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <CartList :products="products">
          <div v-if="productsCartLoadingStatus">
            <img
              class="product__preloader"
              src="/moire/img/preloader.gif"
              alt="Идет загрузка.."
            />
          </div>

          <div class="product__loading-error" v-if="productsCartLoadingFailed">
            Произошла ошибка при загрузке

            <button class="product__reload-btn" @click.prevent="products">
              Попробовать еще раз
            </button>
          </div>
        </CartList>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ numberFormat(totalPrice) }} ₽</span>
          </p>

          <RouterLink
            :to="{ name: 'order' }"
            class="cart__button button button--primery"
            type="submit"
            v-show="showing"
          >
            Оформить заказ
          </RouterLink>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartList from '@/components/cart/CartList.vue'
import numberFormat from '@/helpers/numberFormat'
import { mapGetters, mapState } from 'vuex'

export default {
  components: {
    CartList
  },
  methods: {
    numberFormat
  },
  computed: {
    ...mapGetters('products', {
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalAmount: 'getTotalAmount'
    }),
    ...mapState('products', {
      productsCartLoadingStatus: state => state.productsCartLoadingStatus,
      productsCartLoadingFailed: state => state.productsCartLoadingFailed
    }),
    // eslint-disable-next-line vue/return-in-computed-property
    showing () {
      if (this.products.length > 0) {
        return true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.product {
  position: relative;
  height: 100px;

  &__preloader {
    position: absolute;
    padding: 30px;
    left: 30%;
    top: 60%;
    transform: translate(50%, 50%);
  }

  &__loading-error {
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 15%;
    left: 43%;
  }

  &__reload-btn {
    margin-top: 20px;
    background-color: transparent;
    border: 1px solid #222;
    padding: 15px;
    color: #222;
    font-size: 14px;
    cursor: pointer;
  }
}

.cart__button {
  text-align: center;
}
</style>
