import VFormField from '@/components/VFormField'

export default {
  props: {
    title: {
      type: String,
      required: true
    },
    error: {
      type: String
    },
    placeholder: {
      type: String,
      required: true
    }
  },
  components: {
    VFormField
  },
  emits: [
    'update:modelValue'
  ],
  methods: {
    updateValue (event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
